.buss {
  margin-bottom: 100px;
  &__wrapper {
    display: grid;
    grid-template-columns: 190px 1fr 330px;
    gap: 20px;

    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }

  .left,
  .right {
    margin-top: 40px;
  }

  .info {
    display: flex;
    flex-direction: column;

    &-title {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 14px;
      line-height: 97%;
      letter-spacing: -0.02em;
      color: var(--gray);
      margin-bottom: 22px;
    }

    &-nav {
      ul {
        display: flex;
        flex-direction: column;
        gap: 12px;

        a {
          display: flex;
          align-items: center;
          gap: 8px;
          background: var(--blue);
          border-radius: 10px;
          padding: 10px;
          color: var(--accent);
          transition: var(--transition);
          font-family: var(--font-family);
          font-weight: 500;
          font-size: 14px;
          line-height: 97%;
          letter-spacing: -0.01em;
          svg {
            fill: var(--accent);
          }
          @media (any-hover: hover) {
            &:hover {
              svg {
                fill: var(--white);
              }
              background: var(--accent);
              color: var(--white);
            }
          }
        }
      }
    }
  }

  .center {
    min-width: 0;
  }

  .content {
    &-title {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: clamp(22px, 3vw, 30px);
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--gray);
      margin-bottom: 20px;
    }

    .buss-slide {
      box-sizing: border-box;
      border-radius: 20px;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
      }
    }

    .pagination-buss {
      display: flex;
      justify-content: center;
    }

    .content-slider-navigation {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 10px;
      margin-top: 15px;
      .button-nav {
        width: 57px;
        height: 27px;
        display: grid;
        place-items: center;
        border-radius: 5px;
        background: var(--blue);
        cursor: pointer;
        svg {
          pointer-events: none;
        }

        @media (any-hover: hover) {
          &:hover {
            background: var(--accent);
            svg path {
              fill: var(--white);
            }
          }
        }

        &.swiper-button-disabled {
          opacity: 0.5;
          pointer-events: none;
        }
      }
    }
  }

  .content-text {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-top: 35px;
    border-radius: 20px;
    padding: 23px 30px;
    background: #f8f8f8;
    margin-bottom: 50px;

    @media (max-width: 992px) {
      padding: 20px 10px;
    }
    p {
      margin: 0;
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 16px;
      line-height: 136%;
      letter-spacing: -0.02em;
      color: var(--gray);
    }
  }

  .content-chars {
    margin-bottom: 50px;
    &-title {
      font-family: var(--font-family);
      font-weight: 600;
      font-size: 22px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--gray);
      margin-bottom: 28px;
    }

    .chars-block-items {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 30px;
    }

    .chars-block {
      border-radius: 20px;
      background: var(--blue);
      padding: 30px;
      @media (max-width: 992px) {
        padding: 20px 10px;
        border-radius: 10px;
      }
      &:not(:last-child) {
        margin-bottom: 15px;
      }

      .total {
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 22px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--accent);
        margin-top: 40px;
      }
    }

    .chars-block-title {
      font-family: var(--font-family);
      font-weight: 600;
      font-size: 22px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--accent);
      margin-bottom: 30px;
    }

    .chars-block-item {
      display: grid;
      grid-template-columns: 41px 1fr;
      gap: 12px;

      &-icon {
        width: 41px;
        height: 41px;
        display: grid;
        place-items: center;
        background: var(--white);
        border-radius: 5px;
        svg {
          fill: var(--accent);
          width: 20px;
          height: 20px;
        }
      }

      p {
        display: flex;
        flex-direction: column;
        gap: 5px;
        margin: 0;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 16px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--gray);
        span {
          font-family: var(--font-family);
          font-weight: 400;
          font-size: 14px;
          line-height: 97%;
          letter-spacing: -0.01em;
          color: var(--gray);
          opacity: 0.5;
        }
      }
    }
  }

  .content-about {
    margin-bottom: 40px;
    img {
      border-radius: 20px;
      height: 483px;
      width: 100%;
      @media (max-width: 767px) {
        height: 100%;
      }
    }
  }

  .content-futures-wrapper {
    border-radius: 20px;
    padding: 23px 30px;
    background: #fdffdd;

    ul {
      display: flex;
      flex-direction: column;
      gap: 20px;

      li {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 16px;
        line-height: 136%;
        letter-spacing: -0.02em;
        color: var(--gray);
      }
    }

    p {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 16px;
      line-height: 136%;
      letter-spacing: -0.02em;
      color: var(--gray);
    }
  }

  .buss-cart {
    border-radius: 20px;
    background: #f8f8f8;
    padding: 40px 20px 16px 20px;

    .buss-cart-price {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: clamp(22px, 3vw, 30px);
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--accent);
      margin-bottom: 32px;
    }

    .buss-cart-list {
      display: flex;
      flex-direction: column;
      gap: 20px;
      margin-bottom: 30px;
    }

    .buss-cart-li {
      display: flex;
      align-items: center;
      gap: 10px;

      font-family: var(--font-family);
      font-weight: 500;
      font-size: 16px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--gray);

      span {
        opacity: 0.53;
      }
    }
  }

  .buss-cart-actions {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 42px;
    .buss-cart-actions-btn {
      display: grid;
      place-items: center;
      border-radius: 100%;
      min-width: 38px;
      height: 38px;
      background: var(--blue);

      transition: var(--transition);

      @media (any-hover: hover) {
        &:hover {
          background: var(--accent);
          svg path {
            stroke: var(--white);
          }
        }
      }
    }

    .buss-cart-actions-btn-docs {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 12px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--gray);
      text-align: left;
      border: 1px solid #ddd;
      border-radius: 10px;
      background: var(--white);
      padding: 7px 63px 7px 10px;
      position: relative;
      &:after {
        content: "";
        position: absolute;
        right: 0;
        bottom: 0;
        width: 64px;
        height: 46px;
        background: url("../../img/icons/doc-icon.webp") center / cover
          no-repeat;
      }
    }
  }

  .buss-cart-cb {
    display: flex;
    flex-direction: column;
    gap: 10px;

    .buss-cart-cb-head {
      display: grid;
      grid-template-columns: 58px 1fr;
      gap: 10px;
      align-items: center;
      background: var(--white);
      padding: 10px 20px;
      border-radius: 10px;

      .photo {
        border-radius: 100px;
        overflow: hidden;
        max-width: 58px;
      }

      .name {
        display: flex;
        flex-direction: column;
        gap: 4px;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 18px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--gray);

        span {
          font-family: var(--font-family);
          font-weight: 500;
          font-size: 12px;
          line-height: 97%;
          letter-spacing: -0.01em;
          color: var(--gray);
          opacity: 0.5;
        }
      }
    }

    .btn-callback {
      background: var(--blue);
      color: var(--accent);
      padding: 12px;
      @media (any-hover: hover) {
        &:hover {
          background: var(--accent);
          color: var(--white);
        }
      }
      &:active {
        background: var(--active);
        color: var(--white);
      }
    }

    .btn-bussines {
      background: var(--accent);
      color: var(--white);
      padding: 12px;
      margin-bottom: 20px;
      @media (any-hover: hover) {
        &:hover {
          background: var(--hovered);
        }
      }

      &:active {
        background: var(--active);
        color: var(--white);
      }
    }
  }

  .content-beniffits {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 13px;
    margin-bottom: 50px;
    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }

    .content-future {
      display: flex;
      flex-direction: column;
      border-radius: 20px;
      background: #fdffdd;
      gap: 20px;
      padding: 20px;
      .head {
        display: flex;
        align-items: center;
        gap: 5px;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 14px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--gray);
      }
      .bottom {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 25px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--tekst);
      }
    }
  }

  .content-included {
    display: flex;
    flex-direction: column;
    gap: 23px;

    .content-included-block-title {
      border-radius: 20px;
      background: #fdffdd;
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 25px;
      line-height: 136%;
      letter-spacing: -0.02em;
      color: var(--gray);
      padding: 13px 32px;
    }

    ul {
      display: flex;
      flex-direction: column;
      gap: 18px;
      li {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 16px;
        line-height: 136%;
        letter-spacing: -0.02em;
        color: var(--gray);
      }
    }
  }

  .content-form {
    border-radius: 20px;
    background: var(--accent);
    padding: 28px 50px;
    margin-bottom: 40px;
    .content-form-wrapper {
      background: var(--white);
      padding: 30px;
      border-radius: 20px;
      position: relative;

      .content-form-top {
        display: grid;
        grid-template-columns: 1fr 50px;
        align-items: center;
        gap: 20px;
        margin-bottom: 35px;
        p {
          font-family: var(--font-family);
          font-weight: 600;
          font-size: 18px;
          font-size: clamp(12px, 2vw, 18px);
          line-height: 97%;
          letter-spacing: -0.01em;
          color: var(--gray);
          margin: 0;
        }

        .photo {
          position: relative;

          &:after {
            content: "";
            position: absolute;
            bottom: 5px;
            right: 5px;
            width: 8px;
            height: 8px;
            background: #5dff30;
            border-radius: 50%;
          }
        }
      }

      .form-content {
        display: flex;
        flex-direction: column;
        gap: 14px;

        input {
          padding: 16px 21px;
        }

        .form-content-btn {
          padding-block: 17px;
          background: var(--accent);
          color: var(--white);

          @media (any-hover: hover) {
            &:hover {
              background: var(--hovered);
            }
          }
        }

        .policy {
          position: relative;
          input {
            position: absolute;
            inset: 0;
            z-index: 1;
            cursor: pointer;
            appearance: none;
            opacity: 0;
          }
          label {
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 12px;
            line-height: 136%;
            letter-spacing: -0.02em;
            color: var(--gray);
            display: block;
            position: relative;
            padding-left: 28px;
            a {
              text-decoration: underline;
              text-decoration-skip-ink: none;
              color: inherit;
            }

            &:before {
              content: "";
              position: absolute;
              top: 0;
              left: 0;
              width: 17px;
              height: 17px;
              border: 1px solid #c1c1c1;
              -webkit-transition: var(--transition);
              transition: var(--transition);
              display: -ms-grid;
              display: grid;
              place-items: center;
            }
          }

          input:checked + label:before {
            content: "✓";
            color: var(--white);
            background: var(--accent);
            border-color: transparent;
          }
        }
      }
    }
  }

  .content-steps {
  }

  .content-steps {
    margin-bottom: 40px;
    margin-top: 40px;
    .list {
      display: flex;
      flex-direction: column;
      gap: 15px;
      margin-bottom: 40px;
      li {
        b {
          font-weight: bold;
        }
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 16px;
        line-height: 136%;
        letter-spacing: -0.02em;
        color: var(--gray);
        border-radius: 20px;
        background: var(--blue);
        padding: 20px;
      }
    }
  }

  .content-history {
    margin-bottom: 40px;
    .swiper-slide {
      padding: 20px;
      display: flex;
      flex-direction: column;
      gap: 20px;
      border-radius: 20px;
      background: #fdffdd;
      box-sizing: border-box;
      .year {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: clamp(18px, 2.5vw, 25px);
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--gray);
      }
      .desc {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: clamp(12px, 1.5vw, 14px);
        line-height: 136%;
        letter-spacing: -0.02em;
        color: var(--gray);
      }
    }
    .content-history-nav {
      display: flex;
      justify-content: end;
      gap: 10px;
      margin-bottom: 20px;

      .content-history-nav-btn {
        cursor: pointer;
        transition: 0.3s ease;
        display: grid;
        place-items: center;
        width: 53px;
        height: 27px;
        border-radius: 5px;
        @media (any-hover: hover) {
          &:hover {
            background: var(--blue);
          }
        }
      }
    }
  }

  .content-reviews {
    .rev-slide {
      display: flex;
      flex-direction: column;
      gap: 25px;
      border-radius: 20px;
      background: #f8f8f8;
      padding: 20px;
      box-sizing: border-box;
      .rev-slide-top {
        display: grid;
        grid-template-columns: 51px 1fr;
        gap: 15px;
        align-items: center;

        .name {
          span {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 18px;
            line-height: 97%;
            letter-spacing: -0.01em;
            color: var(--accent);
            display: block;
            margin-bottom: 4px;
          }
          font-family: var(--font-family);
          font-weight: 500;
          font-size: 12px;
          line-height: 97%;
          letter-spacing: -0.01em;
          color: #272727;
          opacity: 0.5;
        }
      }

      .desc {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 16px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--gray);
      }
    }

    .content-reviews-nav {
      display: flex;
      justify-content: end;
      gap: 10px;
      margin-bottom: 20px;

      .content-reviews-nav-btn {
        cursor: pointer;
        transition: 0.3s ease;
        display: grid;
        place-items: center;
        width: 53px;
        height: 27px;
        border-radius: 5px;
        @media (any-hover: hover) {
          &:hover {
            background: var(--blue);
          }
        }
      }
    }
  }
}

.buss-cb {
  position: relative;
  padding-block: 50px;
  margin-bottom: 120px;
  &__bg {
    position: absolute;
    inset: 0;
    z-index: -1;

    img {
      width: 100%;
      height: 100%;
    }
  }

  &__title {
    color: var(--white);
    margin: 0;
    text-align: center;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: clamp(25px, 3vw, 45px);
    line-height: 97%;
    letter-spacing: -0.01em;
    margin-bottom: 16px;
  }

  &__desc {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 97%;
    letter-spacing: -0.01em;
    text-align: center;
    color: var(--white);
    max-width: 650px;
    margin: 0 auto;
    margin-bottom: 56px;
  }

  .form-buss {
    &-row {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 14px;
      margin-bottom: 16px;
      @media (max-width: 992px) {
        grid-template-columns: 1fr;
      }
      input {
        width: 100%;
        border-radius: 10px;
        background: #c6d6ff;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 14px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--accent);
        border: none;
        padding: 16px 21px;
        outline: none;
        &::placeholder {
          color: inherit;
        }

        &:focus {
          outline: 1px solid var(--accent);
        }
      }
    }
    &__btn {
      background: var(--white);
      color: var(--gray);
      padding: 16px 21px;

      @media (any-hover: hover) {
        &:hover {
          background: var(--hovered);
          color: var(--white);
        }
      }
    }

    .policy {
      max-width: 395px;
      margin-left: auto;
      position: relative;
      @media (max-width: 992px) {
        max-width: 100%;
        margin: 0;
      }
      input {
        opacity: 0;
        appearance: none;
        position: absolute;
        z-index: 1;
        inset: 0;
        cursor: pointer;
      }
      label {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 12px;
        line-height: 136%;
        letter-spacing: -0.02em;
        color: #fff;
        display: block;
        padding-left: 28px;
        &:before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          border: 1px solid #c1c1c1;
          width: 17px;
          height: 17px;
          display: grid;
          place-items: center;
          transition: var(--transition);
        }
        a {
          color: inherit;
          text-decoration: underline;
          text-decoration-skip-ink: none;
          position: relative;
          z-index: 1;
        }
      }

      input:checked + label:before {
        content: "✓";
        color: var(--white);
        background: var(--accent);
        border-color: transparent;
      }
    }
  }
}

.buss-variants {
  margin-bottom: 120px;

  &__title {
    margin-bottom: 30px;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    margin-bottom: 60px;

    @media (max-width: 1200px) {
      grid-template-columns: 1fr 1fr;
    }
  }

  &__tags {
    display: flex;
    flex-wrap: wrap;
    column-gap: 18px;
    row-gap: 21px;

    .tag {
      border-radius: 100px;
      padding: 10px 22px;
      background: var(--blue);
      color: var(--accent);
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 18px;
      line-height: 97%;
      letter-spacing: -0.01em;

      transition: var(--transition);

      @media (any-hover: hover) {
        &:hover {
          background: var(--accent);
          color: var(--white);
        }
      }

      &.active {
        background: var(--accent);
        color: var(--white);
      }
    }
  }
}

.buss-subscribe {
  margin-bottom: 130px;
  &__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;

    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }

  .col {
    display: flex;
    flex-direction: column;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 45px;
    line-height: 97%;
    letter-spacing: -0.01em;
    color: var(--gray);
    margin-bottom: 40px;
    span {
      color: #afafaf;
    }
  }

  &__desc {
    border-radius: 100px;
    padding: 10px 22px;
    background: var(--blue);
    display: flex;
    align-items: center;
    gap: 5px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 97%;
    letter-spacing: -0.01em;
    color: var(--accent);
    max-width: fit-content;
    margin-top: auto;
    svg {
      width: 18px;
      height: 18px;
      fill: var(--accent);
    }
  }

  .col-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    backdrop-filter: blur(4.699999809265137px);
    background: #f8f8f8;
    padding: 20px;
    gap: 20px;
  }

  .form-subscribe {
    display: flex;
    flex-direction: column;
    gap: 10px;
    input {
      padding-block: 20px;
    }
    &__btn {
      background: var(--accent);
      color: var(--white);
      padding-block: 17px;
      margin-bottom: 10px;

      @media (any-hover: hover) {
        &:hover {
          background: var(--hovered);
        }
      }
    }

    .policy {
      position: relative;

      input {
        position: absolute;
        inset: 0;
        z-index: 1;
        cursor: pointer;
        appearance: none;
      }
      label {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 12px;
        line-height: 136%;
        letter-spacing: -0.02em;
        color: #4e4e4e;
        display: block;
        position: relative;
        padding-left: 28px;
        a {
          text-decoration: underline;
          text-decoration-skip-ink: none;
          position: relative;
          z-index: 2;
          color: inherit;
        }

        &:before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 17px;
          height: 17px;
          border: 1px solid #c1c1c1;
          transition: var(--transition);
          display: grid;
          place-items: center;
        }
      }

      input:checked + label:before {
        content: "✓";
        color: var(--white);
        background: var(--accent);
        border-color: transparent;
      }
    }
  }

  .sub-image {
    height: 100%;

    img {
      width: 100%;
      height: 100%;
      border-radius: 20px;
    }
  }
}

.invest .buss__wrapper {
  display: grid;
  grid-template-columns: 1fr 387px;
}

.invest .content-chars .chars-block-item-icon svg {
  fill: none;
}
